.cardContainer {
  width: 100%;
  border: 1px solid rgb(214, 213, 213);
  border-radius: 8px;
  padding: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  background: white;
}

.cardHeading {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cardId {
  text-transform: uppercase;
  color: grey;
}

.imageContainer {
  position: relative;
  width: 30px;
  height: 30px;
}

.userImage {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.showStatus {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 10px;
  height: 10px;
  background: silver;
  border-radius: 50%;
}

.cardTitle {
  font-weight: 200;
}

.cardTags {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px;
}

.tags {
  width: max-content;
  padding: 2px 8px;
  border-radius: 5px;
  border: 1px solid silver;
  color: silver;
}
.tagsExcl {
  height: 100%;
  width: 15%;
  padding: 2px 8px;
  border-radius: 5px;
  border: 1px solid silver;
  color: grey;
}
